import React from "react";

import clown from "../static/clown_img.png";
class Color_game extends React.Component {
  render() {
    return (
      <>
        <div
          id="Overview"
          className="bg-white md:h-screen h-full flex xl:flex-row flex-col xl:items-stretch items-center md:mt-0 mt-20 relative z-10"
        >
          <div className="xl:w-1/2 w-4/5 relative z-10">
            <img
              src={clown}
              alt=""
              className=" w-2/3 xl:absolute top-28 2xl:-right-28 -right-20 mx-auto z-10"
            />
          </div>

          <div className="xl:w-1/2 w-4/5 my-auto">
            <div className="bg-second-black-color xl:p-16 p-8 2xl:w-1/2 xl:w-2/3  rounded-3xl my-auto">
              <div className="xl:text-4xl text-3xl font-outfit font-black text-center text-white mt-6">
                COLOR GAME
              </div>
              <div className="text-md font-outfit font-bold text-center text-white mt-5 xl:space-y-5 space-y-1">
                <div>
                  Color Game is one of Asia’s popular carnival game, where you
                  bet some money on a color that you think will appear after a
                  die is thrown.
                </div>
                <div>
                  Earn $CVAL Tokens as you win bets, mint the Color Game NFT for
                  more lucrative benefits, and fill your wallet with those $$$.
                </div>
              </div>
              <div className="text-center mt-10">
                <button
                  className={
                    "px-10 py-5 text-xl font-outfit font-bold  bg-second-violet-color  text-white rounded-xl"
                  }
                >
                  WHITEPAPER
                </button>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Color_game;
